import { TOGGLE_VISIBILITY_VIEWPORT__TASK_LIST } from "./actions";

const initialState = {
  visibility_viewport_taskList: false
}

const visibilityToggler = (state = initialState, action) => {
  console.log("Reduce: ", action);

  switch (action.type) {
    case "TOGGLE_VISIBILITY_VIEWPORT__TASK_LIST":
      return (state = {
        ...state,
        visibility_viewport_taskList: action.payload
      });
    default:
      return state;
  }
};

export default visibilityToggler